import React from "react";
import {
  getAuth,
  createUserWithEmailAndPassword,
  signInWithEmailAndPassword,
} from "firebase/auth";
import app from "../login/Firebase";
// 
function SignUpButton() {
  const email = document.getElementById("SignUpEmail").value;
  const password = document.getElementById("SignUpPassword").value;
  const auth = getAuth();
  createUserWithEmailAndPassword(auth, email, password)
    .then((userCredential) => {
      // Signed in
      const user = userCredential.user;
      // ...
    })
    .catch((error) => {
      const errorCode = error.code;
      const errorMessage = error.message;
      // ..
    });
}
// 
//  
function LoginButton() {
  const email = document.getElementById("SignUpEmail").value;
  const password = document.getElementById("SignUpPassword").value;
  const auth = getAuth();
  signInWithEmailAndPassword(auth, email, password)
    .then((userCredential) => {
      // Signed in
      console.log(userCredential);
      const user = userCredential.user;

      // ...
    })
    .catch((error) => {
      const errorCode = error.code;
      const errorMessage = error.message;
    });
}
//  
const login = () => {
  return (
    <from>
      <h1></h1>
      <div>
        email : <input type="email" id="SignUpEmail" />
      </div>
      <div>
        password : <input type="password" id="SignUpPassword" />
      </div>
      <button onClick={SignUpButton} type="submit" id="SignUpButton">
         
      </button>
      <button onClick={LoginButton}> </button>
    </from>
  );
};

export default login;
